.App {
  text-align: center;
}

#walkClock{
  align-items: center;
  margin: 0 auto;
}

#footerBox{
  background-color: rgb(177, 174, 174);
  font-size: 0.6875rem;
  margin-bottom: 0.3125rem;
  padding-top: 50px;
  padding-bottom: 30px;
  font-family: "ZohoPuvi", "Helvetica", sans-serif;
}
#table_timeStampResult{
  border:1px solid black;
}

.tableTileNameAlign{
  display: table-cell;
  text-align: center;
  /* vertical-align: inherit; */
}

#inputDateToTimeStamp{

}

.inputBoxDesgin{
  background-color: #ddd;
  font-size: 0.9rem;
  width: 50px;
  /* margin-bottom: 0.3125rem; */
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  /* padding-top: 10px; */
  /* padding-bottom: 10px; */
  /* margin-left: 10%; */
  /* margin-top: 30px; */
  /* margin-right: 10%; */
}

td {
  text-align: center;
  display: table-cell;
}

.dateToTimeStampInput{
  display: inline-block;
  flex-direction:column;
  margin-right: 5px;
}

.dateToTimestampInputAttr{
  cursor: pointer;
  margin-top: 20px;
  width: 80px;
  height: 25px;
}

#dateToTimeStampMonth{
  display: inline-block;
  flex-direction:column;
  margin-right: 20px;
}

.tr_td_padding {
  border:1px solid black;
  padding: 5px;
}

#timeStampToStringResult{
  align-items: center;
  padding-top: 20px;
  padding-bottom: 10px;
}

#inputTimeStampConvert{
  cursor: pointer;
  margin-top: 20px;
  width: 90px;
  height: 30px;
}

::-webkit-input-placeholder {
  text-align: center;
}

:-moz-placeholder {
  text-align: center;
}

input[type="text"]{
    font-size:50px;
    font-weight: bolder;
}

#inputTimeStampClear{
  cursor: pointer;
  margin-top: 20px;
  width: 90px;
  height: 30px;
}

#dateToTimeStampBox{
  background-color: #ddd;
  font-size: 0.9rem;
  /* margin-bottom: 0.3125rem; */
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  padding-top: 10px;
  padding-bottom: 10px;
  margin-left: 10%;
  margin-top: 30px;
  margin-right: 10%;
}

#timeStampToDateBox{
  background-color: #ddd;
  font-size: 0.9rem;
  /* margin-bottom: 0.3125rem; */
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  padding-top: 10px;
  padding-bottom: 10px;
  margin-left: 10%;
  margin-right: 10%;
}

#inputTimeStamp{
  background-color: #fff;
  border-radius: 0.28rem;
  border: 1px solid rgba(34,36,38,.15);
  box-shadow: 0 0 0 0 transparent inset;
  transition: color .1s;
  width: 40%;
  height: 30px;
}

#footerLinks{
  font-size: 1.2em;
}

#copyClipBorad{
  margin-left: 20px;
}

#timeStringShow{
  font-weight: bolder;
}

#timestampShow{
  line-height: .5px;
  font-size: 2.6em;
}

#timestampShowTitle{
  line-height: .5em;
  font-size: 1.3em;
}

a{
  color: #21759a;
  text-decoration: none;
}

#footerCopyRight{
  margin: 0;
  font-size: 1.2em;
}

#headerDescription {
  margin: 0;
  font-size: 1.2em;
}

#headerTitle {
  margin: 0;
  font-size: 2.0em;
}

#headerBox {
  font-family: Lato, 'Helvetica Neue', Arial, Helvetica, sans-serif;
  width: auto;
  border: 15px;
  /* padding: 10px; */
  background-color: #f2f3f7;
}

#maincontainer {
  max-width: 1360px;
  min-height: 100%;
  width: 90%;
  width: calc(100% - 52px);
  margin: 0 auto;
  background-color: #fff;
  border: 1px solid #949494;
  border-top: 0;
  padding: 0 14px;
  box-shadow: 0 0 12px #b4b4b4;
  border-radius: 0 0 5px 5px;
}

input { 
  text-align: center;
  font-size: medium; 
}

@media only screen and (min-width: 1340px) {
  #maincontainer {
    padding: 0 58px;
    width: calc(100% - 160px);
    font-size: 1.0em;
  }
}

@media only screen and (min-width: 1200px) {
  #maincontainer {
    padding: 0 48px;
    box-shadow: 0 0 12px #b4b4b4;
    width: calc(100% - 120px);
    font-size: 1.0em;
  }
}

div {
  display: block;
}

@media only screen and (min-width: 1200px) {
  body {
    font-size: 15px;
  }
}

body {
  font-size: 14px;
  background-color: #d4d9dd;
  font-family: open sans, lucida grande, sans-serif;
  font-weight: 400;
  line-height: 1.55;
  color: #000;
}